import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {CategoryService} from '../../../../../../business/service/Admin/SystemConfig/category.service';
import {UserService} from '../../../../../../business/service/Admin/Auth/user.service';

@Component({
  selector: 'app-new-category',
  templateUrl: './new-category.component.html',
  styleUrls: ['./new-category.component.scss']
})
export class NewCategoryComponent implements OnInit {

  newCategoryForm: FormGroup;
  submitted = false;
  technicianList: any[];
  compareFn: ((f1: any, f2: any) => boolean) | null = this.compareByValue;

  constructor( private router: Router, private formBuilder: FormBuilder, private categoryService: CategoryService,
               private userService: UserService ) { }

  ngOnInit() {
    this.getAllTechnician();
    this.newCategoryForm = this.formBuilder.group({
      name: ['', Validators.required],
      description: ['', Validators.required],
      assigned: new FormControl()
    });
  }

  getAllTechnician() {
    this.userService.getAll().subscribe(
      data => {
        console.log(data);
        this.technicianList = data;
      }
    );
  }

  // convenience getter for easy access to form fields
  get f() { return this.newCategoryForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    // if (this.newCategoryForm.invalid) {
    //   return;
    // }
    console.log(this.newCategoryForm.value);
    this.newCategory(this.newCategoryForm.value);
  }

  onReset() {
    this.submitted = false;
    this.newCategoryForm.reset();
  }

  private newCategory(value: any) {
    this.categoryService.saveCategory(value).subscribe(
      data => {
        console.log(data);
      },
      error => {
        console.log(error);
      },
      () => {
        this.router.navigate(['/gene/admin/system-config/category']);
      }
    );
  }

  cancel() {
    this.router.navigate(['/gene/admin/system-config/category']);
  }

  compareByValue(f1: any, f2: any) {
    return f1 && f2 && f1.id === f2.id;
  }
}
