import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {PriorityService} from '../../../../../../business/service/Admin/SystemConfig/priority.service';

@Component({
  selector: 'app-new-priority',
  templateUrl: './new-priority.component.html',
  styleUrls: ['./new-priority.component.scss']
})
export class NewPriorityComponent implements OnInit {
  newPriorityForm: FormGroup;
  submitted = false;

  constructor( private router: Router, private formBuilder: FormBuilder, private priorityService: PriorityService ) { }

  ngOnInit() {
    this.newPriorityForm = this.formBuilder.group({
      name: ['', Validators.required],
      description: ['', Validators.required],
    });
  }

  // convenience getter for easy access to form fields
  get f() { return this.newPriorityForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    // if (this.newPriorityForm.invalid) {
    //   return;
    // }
    console.log(this.newPriorityForm.value);
    this.newPriority(this.newPriorityForm.value);
  }

  onReset() {
    this.submitted = false;
    this.newPriorityForm.reset();
  }

  private newPriority(value: any) {
    this.priorityService.savePriority(value).subscribe(
      data => {
        console.log(data);
      },
      error => {},
      () => {
        this.router.navigate(['/gene/admin/system-config/priority']);
      }
    );
  }

  cancel() {
    this.router.navigate(['/gene/admin/system-config/priority']);
  }
}
