import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {DepartmentService} from '../../../../../../business/service/Admin/SystemConfig/department.service';

@Component({
  selector: 'app-new-department',
  templateUrl: './new-department.component.html',
  styleUrls: ['./new-department.component.scss']
})
export class NewDepartmentComponent implements OnInit {

  newDepartmentForm: FormGroup;
  submitted = false;

  constructor( private router: Router, private formBuilder: FormBuilder, private departmentService: DepartmentService ) { }

  ngOnInit() {
    this.newDepartmentForm = this.formBuilder.group({
      name: ['', Validators.required],
      description: ['', Validators.required],
    });
  }

  // convenience getter for easy access to form fields
  get f() { return this.newDepartmentForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    // if (this.newDepartmentForm.invalid) {
    //   return;
    // }
    console.log(this.newDepartmentForm.value);
    this.newDepartment(this.newDepartmentForm.value);
  }

  onReset() {
    this.submitted = false;
    this.newDepartmentForm.reset();
  }

  private newDepartment(value: any) {
    this.departmentService.saveItem(value).subscribe(
      data => {
        console.log(data);
      },
      error => {},
      () => {
        this.router.navigate(['/gene/admin/system-config/department']);
      }
    );
  }

  cancel() {
    this.router.navigate(['/gene/admin/system-config/department']);
  }
}
