import { Component, OnInit } from '@angular/core';
import {StatusService} from '../../../../../../business/service/Admin/SystemConfig/status.service';

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.scss']
})
export class StatusComponent implements OnInit {

  rows: any[] = [];
  constructor(private categoryService: StatusService) { }

  ngOnInit() {
    this.fetchCategories();
  }

  fetchCategories() {
    this.categoryService.getAll().subscribe( data => {
        this.rows = data;
        console.log(data);
      },
      error => {
        console.log(error);
      });
  }


}
