import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {DashboardComponent} from '../../dashboard/dashboard.component';
import {SystemConfigDashboardComponent} from './system-config-dashboard/system-config-dashboard.component';
import {CategoryComponent} from './category/category.component';
import {NewCategoryComponent} from './new-category/new-category.component';
import {NewPriorityComponent} from './new-priority/new-priority.component';
import {PriorityComponent} from './priority/priority.component';
import {StatusComponent} from './status/status.component';
import {NewStatusComponent} from './new-status/new-status.component';
import {DepartmentComponent} from './department/department.component';
import {NewDepartmentComponent} from './new-department/new-department.component';


const routes: Routes = [
  {
    path: '',
    data: {
      title: 'System Configuration'
    },
    children: [
      {
        path: '',
        redirectTo: 'system-config'
      },
      {
        path: 'system-config',
        component: SystemConfigDashboardComponent,
        data: {
          title: 'System Configuration'
        }
      },
      {
        path: 'category',
        component: CategoryComponent,
        data: {
          title: 'Category'
        }
      },
      {
        path: 'new-category',
        component: NewCategoryComponent,
        data: {
          title: 'New Category'
        }
      }
      ,
      {
        path: 'department',
        component: DepartmentComponent,
        data: {
          title: 'Department'
        }
      },
      {
        path: 'new-department',
        component: NewDepartmentComponent,
        data: {
          title: 'New Department'
        }
      },
      {
        path: 'priority',
        component: PriorityComponent,
        data: {
          title: 'Priority'
        }
      },
      {
        path: 'new-priority',
        component: NewPriorityComponent,
        data: {
          title: 'New Priority'
        }
      },
      {
        path: 'status',
        component: StatusComponent,
        data: {
          title: 'Status'
        }
      },
      {
        path: 'new-status',
        component: NewStatusComponent,
        data: {
          title: 'New Status'
        }
      }
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SystemConfigRoutingModule { }
