import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {StatusService} from '../../../../../../business/service/Admin/SystemConfig/status.service';

@Component({
  selector: 'app-new-status',
  templateUrl: './new-status.component.html',
  styleUrls: ['./new-status.component.scss']
})
export class NewStatusComponent implements OnInit {

  newStatusForm: FormGroup;
  submitted = false;

  constructor( private router: Router, private formBuilder: FormBuilder, private statusService: StatusService ) { }

  ngOnInit() {
    this.newStatusForm = this.formBuilder.group({
      name: ['', Validators.required],
      description: ['', Validators.required],
    });
  }

  // convenience getter for easy access to form fields
  get f() { return this.newStatusForm.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    // if (this.newStatusForm.invalid) {
    //   return;
    // }
    console.log(this.newStatusForm.value);
    this.newStatus(this.newStatusForm.value);
  }

  onReset() {
    this.submitted = false;
    this.newStatusForm.reset();
  }

  private newStatus(value: any) {
    this.statusService.saveStatus(value).subscribe(
      data => {
        console.log(data);
      },
      error => {
        console.log(error);
      },
      () => {
        this.router.navigate(['/gene/admin/system-config/status']);
      }
    );
  }

  cancel() {
    this.router.navigate(['/gene/admin/system-config/status']);
  }
}
